body {
  background: #F2DFD7;
  color: #1D201F;
  font-family: 'Cutive', serif;
  padding: 0;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  text-decoration: none;
  color: #1D201F;
}

p {
  line-height: 150%;
}

ul {
  list-style: none;
  padding-left: 0;
}